import { getCached, setCached } from "./cache";

const NEWS_API_BASE = process.env.NEWS_API_BASE || "https://newsapi.org/v2";
const NEWS_API_KEY = process.env.NEWS_API_KEY;

/**
 * Fetch news from NewsAPI with caching
 * @param {string} endpoint - API endpoint (e.g., 'top-headlines', 'everything')
 * @param {Object} params - Query parameters
 * @returns {Promise<Object>} - API response
 */
export async function fetchNews(endpoint, params = {}) {
  // Create cache key from endpoint and params
  const cacheKey = `${endpoint}-${JSON.stringify(params)}`;

  // Check cache first
  const cached = getCached(cacheKey);
  if (cached) {
    console.log("[Cache Hit]", cacheKey);
    return cached;
  }

  // Build query string
  const queryParams = new URLSearchParams({
    ...params,
    apiKey: NEWS_API_KEY,
  });

  const url = `${NEWS_API_BASE}/${endpoint}?${queryParams}`;

  try {
    console.log("[API Call]", endpoint, params, url);
    const response = await fetch(url, {
      next: { revalidate: 60 }, // ISR caching
    });

    if (!response.ok) {
      // If rate limited, try to return stale cache
      if (response.status === 429) {
        console.warn("[Rate Limited] Attempting to use stale cache");
        const staleCache = getCached(cacheKey);
        if (staleCache) return staleCache;
      }

      throw new Error(
        `NewsAPI error: ${response.status} ${response.statusText}`
      );
    }

    const data = await response.json();

    // Cache the response for 60 seconds
    setCached(cacheKey, data, 60000);

    return data;
  } catch (error) {
    console.error("[Fetch Error]", error);

    // Try to return stale cache on error
    const staleCache = getCached(cacheKey);
    if (staleCache) {
      console.log("[Using Stale Cache]", cacheKey);
      return staleCache;
    }

    throw error;
  }
}

/**
 * Fetch top headlines by country
 */
export async function fetchTopHeadlines(country = "ng", category = null) {
  const params = { country };
  if (category) params.category = category;

  return fetchNews("top-headlines", params);
}

/**
 * Fetch headlines by category
 */
export async function fetchByCategory(category) {
  // Special handling for crypto
  if (category === "crypto") {
    return fetchNews("everything", {
      q: "crypto",
      sortBy: "publishedAt",
      language: "en",
    });
  }

  return fetchNews("top-headlines", {
    category,
    language: "en",
  });
}

/**
 * Search news articles
 */
export async function searchNews(query) {
  return fetchNews("everything", {
    q: query,
    sortBy: "publishedAt",
    language: "en",
  });
}

/**
 * Get article by title
 */
export async function getArticleByTitle(title) {
  const data = await searchNews(`"${title}"`); // Quote title for exact phrase match

  if (!data.articles || data.articles.length === 0) {
    return null;
  }

  // Find exact title match
  const article = data.articles.find((a) => a.title === title);
  return article || data.articles[0]; // Fallback to first result
}

/**
 * Sort articles by newest first
 */
export function sortByNewest(articles) {
  if (!articles || !Array.isArray(articles)) return [];

  return articles.sort((a, b) => {
    const dateA = new Date(a.publishedAt);
    const dateB = new Date(b.publishedAt);
    return dateB - dateA;
  });
}
